/* Global Styles */
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  background-color: #000;
  color: #e0e0e0;
}

/* Common Styles for Containers */
.app-container,
.results-container,
.footer-container {
  width: 100%;
  background-color: #000;
}

.app-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  min-height: 100vh;
  padding: 20px;
}

.results-container {
  max-width: 100%;
  padding: 9px;
  border-radius: 8px;
  box-shadow: 0 2px 10px rgba(212, 9, 9, 0.5);
  display: flex;
  flex-direction: column;
}

.footer-container {
  padding: 15px;
  text-align: center;
  margin-top: 30px;
  font-size: 0.9rem;
}

/* Title */
.app-title {
  font-size: 2rem;
  font-weight: bold;
  letter-spacing: 2px;
  margin-bottom: 20px;
  text-align: center;
  /* Center title */
}

/* Search Bar */
.search-bar-container {
  display: flex;
  gap: 10px;
  margin-bottom: 30px;
  justify-content: center;
  /* Center the search bar */
}

.search-input,
.search-button {
  padding: 12px;
  font-size: 1rem;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.search-input {
  width: 300px;
  border: 1px solid #555;
  background-color: #1a1a1a;
  color: #e0e0e0;
}

.search-input:focus {
  background-color: #3e3e3e;
  outline: none;
}

.search-button {
  border: none;
  background-color: #d22626a2;
  color: #e0e0e0;
  cursor: pointer;
}

.search-button:hover {
  background-color: #666;
}

/* Specification Links */
.spec-links {
  display: flex;
  gap: 15px;
  margin-bottom: 20px;
}

.spec-links button {
  padding: 10px 20px;
  border: 1px solid #030101;
  background-color: #000;
  color: #e0e0e0;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.spec-links button:hover {
  background-color: #333;
}

/* Specification Content */
.spec-content {
  margin-top: 20px;
  padding: 100px;
  background-color: #000;
  border-radius: 5px;
  color: #e0e0e0;
  max-height: 400px;
  overflow-y: auto;
  width: 1000px;
}

/* Footer */
.footer-warning {
  font-size: 1rem;
  font-weight: bold;
  color: red;
}

/* Back Button */
.back-button {
  background-color: #1a1a1a;
  color: #e0e0e0;
  border: none;
  padding: 10px 15px;
  font-size: 1rem;
  cursor: pointer;
  border-radius: 5px;
  position: left;
  top: 10px;
  right: 10px;
}

.back-button:hover {
  background-color: #333;
}

/* Additional Styles for Bullet Points in Data */
.spec-content ul {
  padding-left: 20px;
  line-height: 1.8;
  /* More room between the bullet points */
}

.spec-content ul li {
  margin-bottom: 15px;
  /* Add space between list items */
}